@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: radial-gradient( circle farthest-corner at -14.3% -17.5%,  rgba(245,168,168,1) 4%, rgba(164,168,248,1) 100.2% );
  background-image: radial-gradient( circle farthest-corner at 10% 20%,  rgba(163,175,243,1) 0%, rgba(220,182,232,1) 100.2% );

}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}
h1 {
  font-family: 'Kaushan Script', sans-serif;
  text-align: center;
}
.wrapper {
  margin-top: 10px;
  border: 1px solid white;
  border-bottom-right-radius: 50px;
  height: 80px;
}
.input-field >label {
  margin-top: 10px;
}
#new-todo {
  margin-top: 10px;
}
.btnAddTodo {
  background-image: radial-gradient( circle farthest-corner at -4% -12.9%,  rgba(74,98,110,1) 0.3%, rgba(30,33,48,1) 90.2% );
  width: 120px;
  margin-top: 20px;
}
.todolist-item {
  margin: auto;
  border-top: 1px solid white;
  border-bottom: 1px solid whitesmoke;
  border-radius: 10px;
  margin-top: 5px;
  height: 60px;
  text-align: center;
}
.buttons {
  margin-top: 20px;
}
#delicon {
  text-align: center;
  background-color: rgba(233, 210, 210, 0.075);
}

